import React, { Component } from "react";
import "./help.css"


export class Help extends Component {

    constructor(props) {
        super(props);

        this.state = {
            isVisible: false,
        }
    }

    componentDidUpdate(prevProps) {
        if (prevProps.isVisible !== this.props.isVisible) {
            this.setState({ isVisible: this.props.isVisible })
        }
    }

    handleBackgroundClick = (e) => {
        e.stopPropagation();
        this.props.toggleHelpPanel()
    }

    handlePanelClick = (e) => {
        e.stopPropagation();
    }



    render() {
        if (this.state.isVisible) {
            return (
                <div className="Background" onClick={this.handleBackgroundClick}>
                    <div className="HelpContainer" onClick={this.handlePanelClick}>
                        <h2 className="Title">Welcome To My Pathfinding Visualizer</h2>

                        <div className="Card">THIS IS A WORK IN PROGRESS</div>

                        <div className="NavBar">
                            <button>previous</button>
                            <span>0</span>
                            <button>next</button>
                        </div>
                    </div>
                </div >

            );
        } else {
            return (
                <div></div>
            )
        }

    }
}